<template>
  <div class="page">
    <a-breadcrumb>
      <a-breadcrumb-item>
        <router-link to="/">Home</router-link>
      </a-breadcrumb-item>
      <a-breadcrumb-item>
        <router-link to="/computer">计算机</router-link>
      </a-breadcrumb-item>
    </a-breadcrumb>
    <a-list itemLayout="vertical" size="large" :dataSource="articles">
      <a-list-item slot="renderItem" slot-scope="item,index" key="item.title">
        <a-list-item-meta :description="item.description">
          <a slot="title">
            <router-link :to="{name: 'Content',params:{class:clazz,article:item.name}}">{{index + 1}}. {{item.name}}</router-link>
          </a>
        </a-list-item-meta>
        {{item.content}}
      </a-list-item>
    </a-list>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    name: "List",
    data: () => {
      return {
        clazz: '',
        articles: []
      }
    },
    created: function () {
      this.clazz = this.$route.params.class;
      axios.get('../computer/' + this.clazz + '.json').then(resp => {
        this.articles = resp.data;
      });
    }
  }
</script>

<style scoped>
  .page {
    width: 800px;
    margin: 80px auto;
  }
</style>